"use client"

import { useState, useEffect } from "react"
import { MdAdd, MdDelete, MdEdit, MdHistory, MdSchedule } from "react-icons/md"
import { useAuth } from "@/context/auth-context"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Alert } from "@/components/ui/alert"
import { motion, AnimatePresence } from "framer-motion"

interface Shift {
  id: number
  date: string
  startTime: string
  endTime: string
  attendant: string
}

export default function AdminShiftsPage() {
  const { user } = useAuth()
  const [shifts, setShifts] = useState<Shift[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [showForm, setShowForm] = useState(false)
  const [formData, setFormData] = useState<Partial<Shift>>({})

  useEffect(() => {
    if (user?.role !== "ADMIN") return

    const fetchShifts = async () => {
      try {
        setLoading(true)
        await new Promise((res) => setTimeout(res, 700))
        const mockShifts: Shift[] = [
          { id: 1, date: "2025-11-05", startTime: "08:00", endTime: "16:00", attendant: "John Doe" },
          { id: 2, date: "2025-11-05", startTime: "16:00", endTime: "00:00", attendant: "Alice Smith" },
          { id: 3, date: "2025-11-06", startTime: "00:00", endTime: "08:00", attendant: "Bob Johnson" },
        ]
        setShifts(mockShifts)
      } catch {
        setError("Failed to load shifts")
      } finally {
        setLoading(false)
      }
    }

    fetchShifts()
  }, [user])

  // Admin actions
  const addShift = () => {
    if (!formData.date || !formData.startTime || !formData.endTime || !formData.attendant) return
    const newShift: Shift = {
      id: shifts.length + 1,
      date: formData.date,
      startTime: formData.startTime,
      endTime: formData.endTime,
      attendant: formData.attendant,
    }
    setShifts((prev) => [...prev, newShift])
    setFormData({})
    setShowForm(false)
  }

  const deleteShift = (id: number) => {
    setShifts((prev) => prev.filter((s) => s.id !== id))
  }

  return (
    <div className="min-h-screen p-6 w-full">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-semibold flex items-center gap-2"><MdSchedule /> Shifts</h1>
        <Button onClick={() => setShowForm((prev) => !prev)} className="flex items-center gap-2 bg-teal-600 hover:bg-teal-700 text-white shadow-lg">
          <MdAdd /> {showForm ? "Cancel" : "Add Shift"}
        </Button>
      </div>

      {error && <Alert variant="destructive">{error}</Alert>}

      <AnimatePresence>
        {showForm && (
          <motion.div initial={{ opacity: 0, height: 0 }} animate={{ opacity: 1, height: "auto" }} exit={{ opacity: 0, height: 0 }} className="mb-6 overflow-hidden">
            <Card className="p-6 bg-gray-800 text-white dark:bg-gray-900 shadow-lg">
              <div className="grid md:grid-cols-4 gap-4">
                <input type="date" value={formData.date || ""} onChange={(e) => setFormData({ ...formData, date: e.target.value })} className="p-2 rounded-xl text-gray-900 dark:text-gray-100" />
                <input type="time" value={formData.startTime || ""} onChange={(e) => setFormData({ ...formData, startTime: e.target.value })} className="p-2 rounded-xl text-gray-900 dark:text-gray-100" />
                <input type="time" value={formData.endTime || ""} onChange={(e) => setFormData({ ...formData, endTime: e.target.value })} className="p-2 rounded-xl text-gray-900 dark:text-gray-100" />
                <input type="text" value={formData.attendant || ""} onChange={(e) => setFormData({ ...formData, attendant: e.target.value })} placeholder="Attendant Name" className="p-2 rounded-xl text-gray-900 dark:text-gray-100" />
              </div>
              <div className="mt-4 flex justify-end">
                <Button onClick={addShift} className="bg-teal-600 hover:bg-teal-700 flex items-center gap-2">
                  <MdAdd /> Add Shift
                </Button>
              </div>
            </Card>
          </motion.div>
        )}
      </AnimatePresence>

      <div className="grid md:grid-cols-3 gap-4">
        {loading ? <p>Loading shifts...</p> : shifts.map((shift) => (
          <motion.div key={shift.id} layout initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0 }} className="bg-gray-800 dark:bg-gray-900 p-4 rounded-2xl shadow-lg flex flex-col justify-between">
            <div className="flex justify-between items-center mb-2">
              <h2 className="font-semibold text-lg">{shift.attendant}</h2>
              <div className="flex gap-2">
                <Button onClick={() => deleteShift(shift.id)} variant="destructive" className="p-1 rounded-full"><MdDelete /></Button>
                <Button variant="secondary" className="p-1 rounded-full"><MdEdit /></Button>
              </div>
            </div>
            <p>Date: {shift.date}</p>
            <p>{shift.startTime} - {shift.endTime}</p>
          </motion.div>
        ))}
      </div>
    </div>
  )
}
